import React from 'react';

export function DrawResult(props) {
    let result = props.state.result;
    let dop = Array(result.length).fill(0);
    if(typeof result === 'object') {
        return <div className='result'>
            <div>
                {dop.map((a,i) =>
                    <div>
                        {result[i].map(e => <input type='text' value={e} key={e}/>)}
                    </div>)
                }
            </div>
        </div>
    }
    else return <div className='result'><input type='text' value={result}/></div>
}
export let count = 0;
export function DrawMatrix(props) {
    let matrix = new Array(Number( props.state.row));
    let q = 0;
    for(let i=0;i<props.state.row;i++)
    {
        matrix[i]=q;
        q = q + Number(props.state.column);
    }
    count = q;
    let ar = new Array(Number( props.state.column));
    ar.fill(0);
    return <div className='matrix'>
        {matrix.map((e,key) => (
                <div key={key}>{
                    ar.map((a,i) =>
                        ( <input id={e + i} key={key + i} type='text'/>)
                    )}
                </div>
            )
        )}
    </div>
}
export function DrawMatrix1(props) {
    let matrix = new Array(Number( props.state.row));
    let q = count;
    for(let i=0;i<props.state.row;i++)
    {
        matrix[i] = q;
        q = q + Number(props.state.column);
    }
    let ar = new Array(Number( props.state.column));
    ar.fill(0);
    return <div className='matrix'>
        {matrix.map((e,key) => (
                <div key={key}>{
                    ar.map((a,i) =>
                        ( <input id={e + i} key={key + i} type='text'/>)
                    )}
                </div>
            )
        )}
    </div>
}
export function SelectSize(props) {
    return <select onChange={props.work}>
        <option>1</option>
        <option>2</option>
        <option>3</option>
        <option>4</option>
        <option>5</option>
        <option>6</option>
    </select>
}



